:root {
  --bg-primary: #1a1a1a;
  --bg-secondary: #333;
  --color-primary: #e6e6e6;
  --color-secondary: gray;
  --bg-ternary: #4d4d4d;
  --bg-quaternary: #666;
}

html {
  font-size: 62.5%;
}

body {
  background-color: var(--bg-primary);
  color: var(--color-primary);
  font-family: Roboto, sans-serif;
  font-size: 1.6rem;
}

header {
  justify-content: center;
  align-items: center;
  gap: 1rem;
  display: flex;
}

header h1 {
  text-align: center;
  font-size: 3rem;
  font-style: oblique;
}

header img {
  border-radius: 50%;
  width: 6rem;
  display: block;
}

.display-none {
  display: none !important;
}

.alert {
  background: #ff6363;
  border-radius: .5rem;
  margin-top: 1rem;
  margin-left: 50%;
  padding: 1rem;
  position: absolute;
  top: 0;
  transform: translateX(-50%);
}

.form-login {
  background: var(--bg-secondary);
  border-radius: 1rem;
  flex-direction: column;
  gap: 2rem;
  width: 35rem;
  margin: 1rem auto auto;
  padding: 2rem;
  display: flex;
}

.form-login div {
  flex-direction: column;
  justify-content: start;
  gap: .5rem;
  display: flex;
}

input {
  color: inherit;
  border: .1rem solid var(--color-secondary);
  font-size: inherit;
  background-color: var(--bg-secondary);
  border-radius: .5rem;
  height: 2.7rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

button {
  font-size: inherit;
  cursor: pointer;
  border-radius: 10rem;
}

.btn {
  background: var(--bg-quaternary);
  color: inherit;
  border: none;
  padding: 1.5rem;
}

.btn:hover {
  background: var(--bg-ternary);
}

.link {
  background-color: var(--bg-secondary);
  color: inherit;
  border: .2rem solid var(--color-secondary);
  padding: 1.5rem;
}

.link:hover {
  inset: 2.3px solid var(--bg-ternary);
}

hr {
  width: 100%;
}

p {
  text-align: center;
}

@media screen and (width <= 50em) {
  .form-login {
    width: 80%;
    margin-top: 2rem;
  }
}
